/* Navbar styling */
.navbar {
    display: flex;
    justify-content: space-between;
    /* Space between buttons and search bar */
    align-items: center;
    /* Center items vertically */
    background-color: transparent;
    /* Transparent background for navbar */
    color: white;
    /* Text color */
    transition: transform 0.3s ease, opacity 0.3s ease;
    /* Smooth transition for both transform and opacity */
    z-index: 2;
    /* Ensure navbar is on top */
    position: relative;
    /* Make it fixed at the top */
    top: 0;
    left: 0;
    width: 100%;
    /* Ensure the navbar spans the full width */
    padding-bottom: 20px;
}

/* Form containing the search bar */
.form {
    display: flex;
    justify-content: space-between;
    /* Space between buttons and search bar */
    align-items: center;
    /* Center items vertically */
    background-color: transparent;
    /* Transparent background */
    width: 100%;
    color: white;
    /* Text color */
}

/* Search input styling */
.search-input {
    width: 100%;
    /* Full width input */
    background-color: transparent;
    /* Transparent background */
    color: white;
    /* White text */
    border: none;
    /* No border */
    font-size: 20px;
    /* Larger font for the input */
}

.search-input:focus {
    outline: none;
    /* Remove default focus outline */
    background-color: transparent;
    /* Ensure background stays transparent */
}

/* Button styling in the navbar */
.nav-button {
    background: transparent;
    /* Transparent background */
    border: none;
    /* No border */
    color: white;
    /* White button text */
    font-size: 18px;
    /* Button font size */
    cursor: pointer;
    /* Pointer cursor on hover */
}

/* Scroll placeholder for when the navbar slides away */
.scroll-placeholder {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* Height of the placeholder */
    background-color: transparent;
    /* Transparent background */
    z-index: 1;
    /* Just below the navbar */
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.3s ease, opacity 0.3s ease;
    /* Smooth slide-in and fade-in */
    transform: translateY(-100%);
    /* Initially hide by moving it upwards */
    opacity: 0;
    /* Initially invisible */
}

/* Placeholder image within the scroll-placeholder */
.scroll-placeholder>img {
    position: fixed;
    top: 0;
    width: 100%;
    /* Ensure the image takes full width */
    height: 20%;
    /* Adjust height to fit the design */
}

/* When the scroll-placeholder becomes visible (after scrolling) */
.scroll-placeholder.visible {
    transform: translateY(0);
    /* Slide in from the top */
    opacity: 1;
    /* Fade in */
}

/* When the navbar is hidden (after scrolling) */
.navbar.hidden {
    transform: translateY(-100%);
    /* Slide out upwards */
    opacity: 0;
    /* Fade out */
}

.page-title {
    padding-left: 2%;
    width: fit-content;
}

/* Styles for the dropdown */
.dropdown-suggestions {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 50%;
    /* Position it at the center */
    transform: translateX(-50%);
    /* Shift it left by 50% of its width to center it */
    width: 85%;
    /* Set width as desired */
    max-height: 200px;
    overflow-y: auto;
    background-color: white;
    border: 1px solid #ccc;
    color: black;
    z-index: 1;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}


.dropdown-item {
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.dropdown-item:hover {
    background-color: #f0f0f0;
}